(function(angular) {
  'use strict';

  angular
  .module('yoophawebapp')
  .controller('templateController', templateController);

    /** @ngInject */
    function templateController($rootScope, $scope, $uibModalInstance, $state, $localStorage, homeService, uploadService, utilities) {
        var mod = this;

        mod.p_inputs = {
          name: { value: '', valid: true },
          gender: 'M',
          age: { value: '', valid: true },
          doctor: { value: '', valid: true },
          clinic: { value: '', valid: true },
          ailments: { value: '', valid: true }
        }
        mod.validatePatient = validatePatient;
        mod.dislike = dislike;
        

        mod.c_rout = function (){
          $uibModalInstance.dismiss('cancel');
          $state.go('home');
        }

        mod.close = function (){
          $uibModalInstance.dismiss('cancel');
        }

        mod.remove = function(){
          homeService.delCart()
            .then(function (res) {
              // console.log(res);
              // console.log(homeService.del);
              // $localStorage['incomp_cart'] = [];
              delete $localStorage.incomp_cart;
              delete $localStorage.masterCart;
              delete $localStorage.cart;
              delete $localStorage.cart_OTC;
              $rootScope.isCartAdded = false;
              mod.close();
              $state.go('uploadPrescription');
              // delete $localStorage.
            })
        }

        mod.continue = function() {

        }

        function validatePatient() {
          var regex = /^[0-9]\d*$/;
          if(mod.p_inputs.name.value != '') {
            if(mod.p_inputs.age.value != '' && regex.test(parseInt(mod.p_inputs.age.value))) {
              if(mod.p_inputs.doctor.value != ''){
                if(mod.p_inputs.clinic.value != ''){
                  if(mod.p_inputs.ailments.value != ''){
                    createPatient();
                  }else {
                    mod.p_inputs.ailments.valid = false;
                  }
                }else {
                  mod.p_inputs.clinic.valid = false;
                }
              } else {
                mod.p_inputs.doctor.valid = false;
              }
            } else {
              mod.p_inputs.age.valid = false;
            }
          } else {
            mod.p_inputs.name.valid = false;            
          }
        }

        function createPatient(){
          var u_temp_data = $localStorage['usr'];
          var data = {
            'name': mod.p_inputs.name.value,
            'doctor': mod.p_inputs.doctor.value,
            'gender': mod.p_inputs.gender,
            'age': mod.p_inputs.age.value,
            'patientID': utilities.genID(u_temp_data.usr_id + Date() + 'patient'),
            'clinic': mod.p_inputs.clinic.value,
            'user': u_temp_data.usr_id,
            'ailments': '2'//mod.p_inputs.ailments.value
          };
          uploadService.createPatient(data)
            .then(function (res){
              $localStorage['patient'] = res.results;
              $uibModalInstance.dismiss('cancel');
            }, function (err) {
              console.log(err);
            });
        }

        function dislike() {
          homeService.getUserFav($localStorage['usr'].usr_id)
            .then(function (res) {
              var loc_fav = res.data.results;
              var loc_shop = $localStorage['selectedShop'];
              $localStorage['fav'] = res.data.results;
              for(var i = 0; i < loc_fav.length; i++){
                if(loc_fav[i].pharmacy == loc_shop.id){
                  uploadService.dislike(loc_fav[i].id, i);
                  break;
                }
              }
              mod.close();
            })
        }

      }
})(angular);
(function() {
  'use strict';

  angular
    .module('yoophawebapp')
    .directive('navBar', navBar);

  /** @ngInject */
  function navBar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      bindToController: {
          searchInput: "=",
          navStyle: '@'
      },
      controller: NavbarController,
      controllerAs: 'nav',
      scope: true
    };

    return directive;

    /** @ngInject */
    function NavbarController($rootScope, $scope, $localStorage, $state, $window, $timeout, mainService, homeService, cartService) {
      var vm = this;
      var scrollListener;
      var toolbarVisibilityCutoff;
      vm.showSearch = true;
      vm.subHeadedStyle = '';
      vm.searchResult = [];
      vm.logout = logout;
      vm.searchShop = searchShop;
      vm.selectShop = selectShop;
      vm.route = route;

      if($localStorage['log_status'])
        $rootScope.logStat = true;

      if($rootScope.currPage == 'searchResult' || $rootScope.currPage == 'home') {
        vm.showSearch = true;
      } else {
        vm.showSearch = false;
      }

      $window.addEventListener('scroll', toggleToolbar, false);

      function selectShop(item){
        $localStorage['selectedShop'] = item;
        if(!$rootScope.isCartAdded){
          // $localStorage['selectedShop'] = item;
          $state.go('uploadPrescription');
        }else {
          homeService.incomplete_alert();
          vm.searchResult = []; 
          vm.input_key = '';
        }
      }

      function route(page){
        $state.go(page);
      }

      function logout() {
        $localStorage.$reset();
        $rootScope.logStat = false;
        cartService.customer = undefined;
        homeService.user = undefined;
        $state.go('main');
      }

      function searchShop() {
        if(vm.input_key.length > 2){
          mainService.getPrediction(vm.input_key)
            .then(function (res) {
              // console.log(res);
              vm.searchResult = res;
            })
        }else {
          vm.searchResult = [];          
        }
      }

      function toggleToolbar() {
        var pos = $window.scrollY;
        if($window.scrollY > toolbarVisibilityCutoff){
          if($window.scrollY > 38){
            $timeout(function() {
              vm.subHeadedStyle = 'hide-mini-header';
            }, 0);
          }
        }else{
          if($window.scrollY <=38) {
            $timeout(function() {
              vm.subHeadedStyle = '';
            }, 0);
          }
        }
        toolbarVisibilityCutoff = pos;
      }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('yoophawebapp')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

