(function(angular) {
  'use strict';

  /**
   * @ngdoc service
   * @name 'Uploading service'
   * @description
   * Service in the yoophaWeb.
   */
  angular.module('yoophawebapp')
    .service('uploadService', uploadService);

  /** @ngInject */
  function uploadService($q, $localStorage, getData, postData, utilities) {

    var service = {};
    var url;
    service.createCart = createCart;
    service.getDrugInfo = getDrugInfo;
    service.createPatient = createPatient;
    service.postLike = postLike;
    service.dislike = dislike;

    function createCart(data){
      url = 'https://www.yoopha.com/shim/cartinfo.php/?&actiontype=1&subtype=3&token=' + $localStorage['usr'].accessTok;
      var deferred = $q.defer();
      postData.async(url, data)
        .then(function(res) {
          // if(res.status == 201 && res.statusText == 'Created')
            deferred.resolve(res.data);
          // else 
            // deferred.reject(res.data);
        });
      return deferred.promise;
    }

    function getSearchData(url){
      var deferred = $q.defer();
      getData.async(url)
        .then(function(res) {
          deferred.resolve(res);
        });
      return deferred.promise;
    }

    function createPatient(data) {
      url = 'http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/customer/create_patientdetails/';
      var deferred = $q.defer();
      postData.async(url, data)
        .then(function(res) {
          if(res.status == 201 && res.statusText == 'Created')
            deferred.resolve(res.data);
          else 
            deferred.reject(res.data);
        });
      return deferred.promise;
    }

    function getDrugInfo(key){
      url = 'https://www.yoopha.com/shim/search.php/?actiontype=2&key=' + key;
      var deferred = $q.defer();
      getData.async(url)
        .then(function(res) {
          deferred.resolve(res);
        });
      return deferred.promise; 
    }

    function dislike(id, index) {
      url = 'http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/pharmacy/' + id + '/delete_personalcustomer/';
      postData.del(url)
        .then(function (res) {
          if(res.status == 204){
            var temp = $localStorage['fav'];
            temp.splice(index, 1);
            $localStorage['fav'] = temp;
          }
        })
    }

    function postLike(data) {
      url = 'http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/pharmacy/create_personalcustomer/';
      var deferred = $q.defer();
      postData.async(url, data)
        .then(function(res) {
          if(res.status == 201 && res.statusText == 'Created')
            deferred.resolve(res.data);
          else 
            deferred.reject(res.data);
        });
      return deferred.promise;
    }

    return service;

  }
})(angular);
(function(angular) {
  'use strict';

  angular
    .module('yoophawebapp')
    .controller('uploadPrescriptionController', uploadPrescriptionController);

  /** @ngInject */
  function uploadPrescriptionController($rootScope, $scope, $http, $localStorage, $state, $uibModal, uploadService, utilities, homeService) {
    var vm = this;
    var imageListener;
    var slides = vm.slides = [];
    var currIndex = 0;
    var img = ['assets/images/img1.png', 'assets/images/img1.png', 'assets/images/img1.png', 'assets/images/img1.png', 'assets/images/img1.png', 'assets/images/img1.png'];
    vm.selectedImages = [];
    vm.drug_key = undefined;
    vm.drug_search_result = [];
    vm.isImageSelected = false;
    vm.myInterval = 4500;
    vm.active = 0;
    vm.orderAll = true;
    vm.selected_drug = [];
    vm.create_cart_presc = [];
    vm.prefill = {};
    vm.fill = { like: '' }; //fill_color
    vm.addFav = addFav;
    vm.routeTo = routeTo;
    vm.prepUpload = prepUpload;
    vm.editSelected = editSelected;
    vm.search_drug = search_drug;
    vm.add_drug = add_drug;
    vm.del_selected_drug = del_selected_drug;
    vm.update_selected_drug_qty = update_selected_drug_qty;

    vm.addSlide = function() {
      var newWidth = 600 + slides.length + 1;
      slides.push({
        image: img[slides.length % 6],
        id: currIndex++
      });
    };

    init();

    function init(){
        var fillCart = $localStorage.incomp_cart;
        var loc_fav = $localStorage['fav'];
        vm.shopDetails = $localStorage['selectedShop'];
        var temp_data = $localStorage.log_status;
        if(!angular.isUndefined(temp_data)){
            if(angular.isDefined(fillCart)){
                vm.prefill = (fillCart[0].cart_type == 1) ? fillCart[0] : fillCart[1];
                if(vm.prefill.pharmacy == vm.shopDetails.id){
                    vm.selected_drug = processRawCart(vm.prefill.cartitems);
                }else{
                    $localStorage.incomp_cart = [];
                    $rootScope.isCartAdded = false;
                    $state.go('home');
                }
            }
        } else {
            openModel();
        }
        //slide loop
        for (var i = 0; i < 4; i++) {
          vm.addSlide();
        }
        // default fav fill
        if(angular.isDefined(loc_fav)){
            for(var i=0; i<loc_fav.length; i++){
                if(loc_fav[i].pharmacy == vm.shopDetails.id) {
                    vm.fill.like = 'fill_color';
                } else {
                    vm.fill.like = '';
                }
            }
        }
    }

    function processRawCart(data){
        var modified = [];
        var temp_data = data.map( function(temp, index){
            modified[index] = {};
            angular.copy(temp, modified[index]);
            delete modified[index].product;
            modified[index].name = temp.product.name
            modified[index].product = temp.product.id;
        });
        return modified;
    }

    function search_drug(){
        if(vm.drug_key.length >= 3) {
            uploadService.getDrugInfo(vm.drug_key)
            .then(function(res) {
                if(res.results.length > 0) {
                    vm.drug_search_result = res.results;
                } else {
                    vm.drug_search_result = [];
                }
            });
        }else {
            vm.drug_search_result = [];
        }
    }

    function del_selected_drug(item) {
        var index = id(vm.selected_drug, item);
        vm.selected_drug.splice(index, 1);
    }

    function update_selected_drug_qty(action, item) {
        if(action && item.quantity <= 100){
            item.quantity++;
        } else {
            if(item.quantity <= 1){
                del_selected_drug(item);
            }else {
                item.quantity--;
            }
        }
    }

    function dataMakeOver(data){
        return {
            'available' : true,
            'evening': true,
            'noon': false,
            'morning': false,
            'product_type': data.type,
            'price': 0,
            'instruction': data.description,
            'quantity': 1,
            'size': 'M',
            'product': data.id,
            'name': data.name
        };
    }

    function add_drug(item){
        var temp_item = dataMakeOver(item);
        var index = id(vm.selected_drug, temp_item);
        if(index == -1) {
            vm.selected_drug.push(temp_item);
            vm.drug_search_result = '';
            vm.drug_key = undefined;
        } else {
            vm.drug_search_result = '';
            vm.drug_key = undefined;
        }
    }

    function id(list, item){
        if(list.length != 0){
            for(var i = 0; i < list.length; i++){
                if(list[i].product == item.product)
                    return i;
            }
            return -1;
        } else {
            return -1;
        }
    }

    function prepUpload() {
        if(vm.selectedImages.length <= 3) {
        	var file = vm.selectedImages;
            var reader = new FileReader();
            reader.readAsDataURL(file[0]);
            reader.onload = function () {
              vm.create_cart_presc.push(
                {
                    'user': $localStorage['usr'].usr_id,
                    'image': reader.result
                });
            };
            reader.onerror = function (error) {
              console.log('Error: ', error);
            };
        } else {
            vm.selectedImages.splice(-1, 1);
            alert('You can choose maximum 3 image for upload');
        }
    }

    function editSelected(index){
    	vm.selectedImages.splice(index, 1);
        vm.create_cart_presc.splice(index, 1);
    }

    function routeTo(flag) {
        $localStorage['selectedDrug'] = vm.selected_drug;
        if($rootScope.isCartAdded){
            console.log('update_cart');
        }else {
            // console.log('create call');
            // if(angular.isDefined($localStorage['patient']))
                create_cart();
            // else
                // showPatient();
        }
    }

    

    function create_cart() {
        if(angular.isUndefined($localStorage['incomp_cart'])) {
            var u_temp_data = $localStorage['usr'];
            var cartID = utilities.genID(u_temp_data.usr_id + Date());
            var data = {
                'pharmacy': $localStorage['selectedShop'].id,
                'user': $localStorage['usr'].usr_id,
                'prescription': vm.create_cart_presc,
                'tot_presc_amt': 0,
                'tot_otc_amount': 0,
                'cart_id': cartID,
                'cartitems': vm.selected_drug,
                'active': true,
                'cart_type': 1,
                'order_all': (vm.orderAll) ? 'A' : 'C'
            };

            uploadService.createCart(data)
                .then(function (res){
                    console.log(res);
                    $localStorage['incomp_cart'] = [res.results];
                    $rootScope.isCartAdded = true;
                    $state.go('OTC');
                }, function(err){
                    alert(err);
                });
        } else {
            $state.go('OTC');
        }
    }

    function openModel() {
        var modalInstance = $uibModal.open({
              animation: true,
              backdrop: 'static',
              templateUrl: 'app/uploadPrescription/loginAlertTemp.html',
              controller: 'ModalInstanceCtrl',
              controllerAs: 'mod',
              size: 'md'
            });

            modalInstance.result.then(function (selectedItem) {
                $state.go('Login');
            }, function () {
              // $log.info('Modal dismissed at: ' + new Date());
            });
    }

    function hasFavID(list, item){
        if(list.length != 0) {
            for(var i=0; i<list.length; i++){
                if(list[i].pharmacy == item.id)
                    return i;
            }
            return -1;
        } else {
            return -1;
        }
    }

    function addFav(){
        var fav = $localStorage['fav'];
        if(angular.isUndefined(fav)){
            $localStorage['fav'] = [];
            fav = [];
        }
        var index = hasFavID(fav, vm.shopDetails); 
        if(index == -1) {
            var data = {
                'like': 'Y',
                'user': $localStorage['usr'].usr_id,
                'pharmacy': $localStorage['selectedShop'].id
            }
            uploadService.postLike(data)
                .then(function (res){
                    console.log(res);
                    vm.fill.like = 'fill_color';
                    $localStorage['fav'].push(res);
                });

        } else {
            // console.log('dislike');
            dislike(); 
        }
    }

    function dislike() {
        var modalInstance = $uibModal.open({
              animation: true,
              backdrop: 'static',
              templateUrl: 'app/components/template/dislikeAlertTemp.html',
              controller: 'templateController',
              controllerAs: 'mod',
              size: 'sm'
            });
        vm.fill.like = '';
    }

    imageListener = $scope.$watch(function () {
        return vm.selectedImages.length;
    }, function(newVal, oldVal) {
    	// console.log(newVal);
    	if(newVal === 0 || newVal === -1) {
    		vm.isImageSelected = false;
    	}else {
    		vm.isImageSelected = true;
    	}
    });
  }
})(angular);
