(function(angular) {
  'use strict';

  angular
    .module('yoophawebapp')
    .controller('otcController', otcController);

  /** @ngInject */
  function otcController($scope, $window, $timeout, $localStorage, $state, $uibModal, otcService, utilities, homeService) {
    var vm = this;
    // var dataUrl = 'assets/JSON/OTC.json';
    var toolbarVisibilityCutoff;
    var pageOtcData = [];
    var mobCurrIndex = 0;
    var totOtcPrice = 0;

    $scope.$on('$destroy', garbageCollection);
    vm.view = {
      showBaby : true,
      showHealth : false,
      showOthers : false
    };
    vm.addedProduct = [];
    vm.tabStyle = 'rel-tab';
    vm.viewData = {
      babyProd : [],
      healthProd : [],
      othersProd : []
    }
    vm.productSlider = {
      page1 : [],
      page2 : [],
      page3 : []
    }
    vm.active = false;
    vm.mobileProductSlider = [];
    vm.select = select;
    vm.changeQty = changeQty;
    vm.routeTo = routeTo;
    vm.create_otc_cart = create_otc_cart;
    vm.addProduct = addProduct;
    $window.addEventListener('scroll', toggleToolbar, false);
    
    init();

    function init() {
      showPatient();
      var pharmacy = $localStorage['selectedShop'];
      otcService.getOtcData(pharmacy.id)
      	.then(function(res) {
          pageOtcData = res.results;
          // console.log(pageOtcData);
          for(var i = 0; i < pageOtcData.length; i++) {
            var temp = pageOtcData[i];
            // console.log(temp);
            if(temp.otcproduct.category == '1'){
              vm.viewData.babyProd.push(makeoverOtc(temp));
            } else if(temp.otcproduct.category == '2'){
              vm.viewData.healthProd.push(makeoverOtc(temp));
            }else {
              vm.viewData.othersProd.push(makeoverOtc(temp));
            }
          }
        });
        homeService.getExistingCart($localStorage['usr'].usr_id)
              .then(function (res) {
                $localStorage['incomp_cart'][0] = homeService.existingCartInfo.results[0];
              });
    }

    function showPatient() {
      if(angular.isUndefined($localStorage['patient'])){
        var modalInstance = $uibModal.open({
              animation: true,
              backdrop: 'static',
              templateUrl: 'app/components/template/patientDetailsTemp.html',
              controller: 'templateController',
              controllerAs: 'mod',
              size: 'lg'
            });
      }
    }

    function loadSlider(whichPage, list){
      var size = 4;
      var currIndex = 0;
      // console.log(vm.productSlider[whichPage]);
      while (list.length > 0)
          vm.productSlider[whichPage].push({arr : list.splice(0, size), id: currIndex++});
      
    }

    function makeoverOtc(data){
      return {
        'size': 'NA',
        'price': data.sale_price,
        'quantity': 0,
        'isAdded': false,
        'otcproduct': data.otcproduct.id,
        'mrp_price': data.mrp_price,
        'productimage': data.otcproduct.productimage,
        'name': data.otcproduct.name,
        'isDiscount': (parseInt(data.mrp_price) > parseInt(data.sale_price)) ? true : false
      }
    }

    function routeTo(){
      $state.go('viewcart');
    }

    function toggleToolbar() {
      var pos = $window.scrollY;
      if($window.scrollY > toolbarVisibilityCutoff){
        // console.log('up goes',$window.scrollY, ':', toolbarVisibilityCutoff);
        //up scroll
        if($window.scrollY >= 10){
          $timeout(function() {
            vm.tabStyle = 'stick-tab';
          }, 0);
        }
      }else{
        if($window.scrollY <= 10){
          $timeout(function() {
            vm.tabStyle = 'rel-tab';
          }, 0);

        }
      }
      toolbarVisibilityCutoff = pos;
    }

    function addProduct(item){
      var index = hasID(vm.addedProduct, item);
      if(vm.addedProduct && index == -1){
        item.isAdded = true;
        item.quantity = 1;
        addTotPrice(item.mrp_price, 1);
        vm.addedProduct.push(item);
        $localStorage['selectedOTC'] = [];
        $localStorage['selectedOTC'] = vm.addedProduct;
      }
    }

    function addTotPrice(sale_price, flag){
      if(flag)
        totOtcPrice += parseInt(sale_price);
      else 
        totOtcPrice -= parseInt(sale_price);
    }

    function hasID(list, item){
      if(!angular.isUndefined(list)){
        for(var i=0; i < list.length; i++){
          var element = list[i];
          if(item.otcproduct == element.otcproduct)
            return i;
        }
      }
      return -1;
    }

    function select(index){
      if(index == 0){
        vm.view = {
          showBaby : true,
          showHealth : false,
          showOthers : false
        }
      } else if(index == 1) {
        vm.view = {
          showBaby : false,
          showHealth : true,
          showOthers : false
        }
      } else if(index == 2){
        vm.view = {
          showBaby : false,
          showHealth : false,
          showOthers : true
        }
      }
    }

    function changeQty(action, item){
      var index = hasID(vm.addedProduct, item);
      if(action) {
        item.quantity++
        addTotPrice(item.mrp_price, 1);
      }else {
        if(item.quantity == 0 || item.quantity == 1){
          item.quantity = 0;
          item.isAdded = false;
          trash(item);
        }else if(item.quantity > 1) {
          item.quantity--;
        }
        addTotPrice(item.mrp_price, 0);
      }
      vm.addedProduct[index] = item;
      $localStorage['selectedOTC'] = vm.addedProduct;
    }

    function create_otc_cart(){
      var u_temp_data = $localStorage['usr'];
      if($localStorage['incomp_cart'].length < 2) {
        var cartID = utilities.genID(u_temp_data.usr_id + Date());
        var data = {
          'cart_type': '2',
          'total_amount': 0,
          'prescription': [],
          'order_all': 'NA',
          'pharmacy': $localStorage['selectedShop'].id,
          'tot_presc_amt': 0,
          'user': $localStorage['usr'].usr_id,
          'otccartitems': vm.addedProduct,
          'active': true,
          'cart_id': cartID,
          'tot_otc_amount': totOtcPrice
        };
        
        otcService.createOtcCart(data)
          .then(function (res) {
              $localStorage['incomp_cart'][1] = res.results;
              routeTo();
              // $state.go('viewcart');
          }, function(err){
            console.log(err);
          })
      } else {
        console.log('update OTC');
        routeTo();
      }
    }

    function trash(data){
      var index = hasID(vm.addedProduct, data);
      vm.addedProduct.splice(index, 1);
      $localStorage['selectedOTC'] = vm.addedProduct;
    }

    function processRawData(page, list){

      var saved = $localStorage['selectedOTC'];
      if(!angular.isUndefined(saved))
        vm.addedProduct  = saved;

      for(var i = 0; i < list.offerProd.length; i++) {
        var index = hasID(vm.addedProduct, list.offerProd[i]);
        if(index != -1) {
          list.offerProd[i].isAdded = vm.addedProduct[index].isAdded;
          list.offerProd[i].qty = vm.addedProduct[index].qty;  
        } else {
          list.offerProd[i].isAdded = false;
          list.offerProd[i].qty = 0;
        }
        vm.mobileProductSlider.push({arr : list.offerProd[i], id: mobCurrIndex++});
      }
      for(var j = 0; j < list.product.length; j++){
        var index = hasID(vm.addedProduct, list.product[j]);
        if(index != -1) {
          list.product[j].isAdded = vm.addedProduct[index].isAdded;
          list.product[j].qty = vm.addedProduct[index].qty; 
        } else {
          list.product[j].isAdded = false;
          list.product[j].qty = 0; 
        }
      }
      loadSlider(page, list.offerProd);
      return list;
    }

    function garbageCollection() {
      // console.log('destroy');
      $window.removeEventListener('scroll', toggleToolbar, false);
    }
  }
})(angular);
