(function(angular) {
  'use strict';


  angular.module('yoophawebapp')
    .service('mainService', mainService);

  /** @ngInject */
  function mainService($q, $http, $localStorage, getData, utilities) {

    var service = {};
    var url; //= 'https://www.yoopha.com/shim/';
    var gMapURL = 'https://maps.googleapis.com/maps/api/geocode/json?latlng=';
    var places = 'https://www.swiggy.com/api/place/autocomplete?input=';
    service.getLocation = getLocation;
    service.getAddressPrediction = getAddressPrediction;
    service.getPrediction = getPrediction;
    service.user = undefined;
    service.existingCartInfo = undefined;
    service.getUser = getUser;
    service.getExistingCart = getExistingCart;


    // function setPosition(position){
    //   console.log(position);
    // }

    // function showError(code){
    //   console.log(code);
    // }

    function returnData(data) {
      var deferred = $q.defer();

      if (!service[data])  {
        getData.asyncHead(url).then(function (response) {
          if (response.length > 0) {
            service[data] = response.data;
            deferred.resolve(response);
          } else if (angular.isObject(response)) {
            service[data] = response.data;
            deferred.resolve(response);
          } else {
            deferred.reject(response);
          }
        });
      } else {
        deferred.resolve(service[data]);
      }

      return deferred.promise;
    }

    function getUser(id){
      url = 'http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/common/users/' + id;
      return returnData('user');
    }

    function getExistingCart(id) {
      url = 'http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/cart/cart/' + id;
      return returnData('existingCartInfo');
    }

    function getPrediction(key){
      // console.log(key);
      url = 'https://www.yoopha.com/shim/search.php?actiontype=1&key=' + key;
      var deferred = $q.defer();
      getData.async(url)
        .then(function(res) {
          // console.log(res);
          deferred.resolve(res.results);
        });
      return deferred.promise;
    }    

    function getAddressPrediction(key){
      var deferred = $q.defer();
      var url = places + key;
      getData.async(url)
        .then(function(res) {
          // console.log(res);
          deferred.resolve(res);
        });
      return deferred.promise;
      // $http.jsonp(url)
      //   .success(function(data){
      //       var f = JSON.stringify(data)
      //       console.log(f);
      //       deferred.resolve(f);
      //   });
        // deferred.resolve('no data');
        // return deferred.promise;
      // return deferred.promise;
    }

    function getLocation() {
      var deferred = $q.defer();
      utilities.getLocation()
      .then(function(pos){
        var lat = pos.coords.latitude;
        var lng = pos.coords.longitude;
        $localStorage['lat_lng_data'] = {
          'lat' : lat,
          'lng' : lng,
        }
        var url = gMapURL + lat + ',' + lng
        getData.async(url)
          .then(function(res) {
            $localStorage['lat_lng_data'].address = res.results[0].formatted_address;
            var sArr = res.results[0].formatted_address.split(',');
            $localStorage['lat_lng_data'].deliveryLocation = sArr[sArr.length - 5].trim() + ',' + sArr[sArr.length - 4].trim();
            deferred.resolve(res);
          })
      }, function(err){
        deferred.reject(err);
      });
      return deferred.promise;
    }

    return service;
  }
})(angular);
