(function(angular) {
  'use strict';

  angular.module('yoophawebapp')
    .service('cartService', cartService);

  /** @ngInject */
  function cartService($q, $localStorage, getData, postData, utilities) {

    var service = {};
    var url = '';

    // service.cart = undefined;
    // service.userData = undefined;
    service.customer = undefined;
    // service.masterCart = undefined;
    // service.getMasterCartID = getMasterCartID;
    service.createCustomer = createCustomer;
    service.getCustomerIfExist = getCustomerIfExist;
    // service.getUserData = getUserData;
    service.createOrder = createOrder;
    // service.createMasterCart = createMasterCart;

    function createOrder(data){
      url = 'https://www.yoopha.com/shim/order.php/?actiontype=2&token=' + $localStorage['usr'].accessTok;
      var deferred = $q.defer();
      postData.async(url, data)
        .then(function(res) {
          // if(res.status == 201 && res.statusText == 'Created')
            deferred.resolve(res.data);
          // else 
            // deferred.reject(res.data);
        });
      return deferred.promise;
    }

    function returnData(data) {
      var deferred = $q.defer();

      if (!service[data])  {
        getData.asyncHead(url).then(function (response) {
          if (response.length > 0) {
            service[data] = response.data;
            deferred.resolve(response);
          } else if (angular.isObject(response)) {
            service[data] = response.data;
            deferred.resolve(response);
          } else {
            deferred.reject(response);
          }
        });
      } else {
        deferred.resolve(service[data]);
      }

      return deferred.promise;
    }

    // function createMasterCart(data){
    //   url = 'http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/cart/create_master_cart/';
    //   var deferred = $q.defer();
    //   postData.async(url, data)
    //     .then(function(res) {
    //       if(res.status == 201 && res.statusText == 'Created')
    //         deferred.resolve(res.data);
    //       else 
    //         deferred.reject(res.data);
    //     });
    //   return deferred.promise;
    // }

    function createCustomer(data){
      url = 'https://www.yoopha.com/shim/user.php/?actiontype=2&subtype=2&token=' + $localStorage['usr'].accessTok;
      var deferred = $q.defer();
      postData.async(url, data)
        .then(function(res) {
          if(res.status == 201 && res.statusText == 'Created')
            deferred.resolve(res.data);
          else 
            deferred.reject(res.data);
        });
      return deferred.promise;
    }

    function getCustomerIfExist(userID){
      service.customer = undefined;
      url = 'https://www.yoopha.com/shim/user.php/?actiontype=2&subtype=1&uid=' + userID + '&token=' + $localStorage['usr'].accessTok;
      return returnData('customer');
    }

    return service;

  }
})(angular);
(function(angular) {
    'use strict';

    angular
        .module('yoophawebapp')
        .controller('cartController', cartController);

    /** @ngInject */
    function cartController($localStorage, $uibModal, cartService, utilities, viewCartService, homeService) {
        var vm = this;
        var user = {};
        vm.otcDetail = [];
        vm.customerDetails = undefined;
        vm.address = {
            'address_line1': '',
            'address_line2': '',
            'address_line3': '',
            'pincode': '',
            'district': 'Chennai',
            'state': 'Tamil Nadu',
            'country': 'India',
            'lat': '0.000',
            'long': '0.000',
            'address_type': 3
        }
        vm.custModel = {};
        vm.tot = 0;
        vm.pageViewLogic = {
            address: {
                header: 'step_complete',
                click_safe: true,
                show: true,
                showNewCust: true,
                showCustAddress: false,
                addNewAddress: false,
                sel_ship_add_id: 0,
                sel_bill_add_id: 0
            },
            review : {
                header: '',
                click_safe: true,
                show: false
            },
            checkout: {
                header: '',
                click_safe: true,
                show: false
            }
        };
        vm.trash = trash;
        vm.changeQty = changeQty;
        vm.updateDrug = updateDrug;
        vm.createCust = createCust;
        vm.createOrder = createOrder;
        vm.prep_for_add = prep_for_add;
        vm.changeView = changeView;
        vm.nextStep = nextStep;
        vm.isUndefined = function(data) {
            // console.log(data , data === undefined);
            return (data === undefined);
        }
        // angular.copy(vm.address, vm.shippingAddress);
        // angular.copy(vm.address, vm.billingAddress);

        init();

        function init() {
            user = $localStorage['usr'];
            vm.customerDetails = $localStorage['cust'];
            vm.pageViewLogic.address.showCustAddress = angular.isDefined(vm.customerDetails) ? true : false;
            vm.custModel.name = user.username;
            vm.custModel.mobile = user.mobile_no;
            vm.custModel.customerID = utilities.genID(user.email);
            vm.custModel.gender = 'M';
            vm.custModel.address = vm.address;
            getCart();
            isCustExist();
            getMasterCartID();
        }

        function defaultAddSel(data){
            console.log(data);
            if(data.address.length > 1){
                for(var i=0; i<data.address.length; i++){
                    if(data.address[i].address_type == 3){
                        vm.pageViewLogic.address.sel_ship_add_id = data.address[i].id;
                        // break;        
                    }else {
                        vm.pageViewLogic.address.sel_bill_add_id = data.address[i].id;
                        // break;
                    }
                }
            } else {
                vm.pageViewLogic.address.sel_ship_add_id = (vm.pageViewLogic.address.showCustAddress) ? data.address[0].id : 0;
                vm.pageViewLogic.address.sel_bill_add_id = vm.pageViewLogic.address.sel_ship_add_id;
            }
        }

        function changeView(ctrl_name) {
            if(vm.pageViewLogic[ctrl_name].click_safe){
                vm.pageViewLogic[ctrl_name].header = 'step_complete';
                vm.pageViewLogic[ctrl_name].show = true;
                if(ctrl_name == 'address') {
                    vm.pageViewLogic['review'].header = '';
                    vm.pageViewLogic['checkout'].header = '';
                    vm.pageViewLogic['review'].show = false;
                    vm.pageViewLogic['checkout'].show = false;
                } else if(ctrl_name == 'review') {
                    vm.pageViewLogic['checkout'].header = '';
                    vm.pageViewLogic['address'].show = false;
                    vm.pageViewLogic['checkout'].show = false;
                } else {
                    vm.pageViewLogic['address'].show = false;
                    vm.pageViewLogic['review'].show = false;
                } 
            }
        }

        function nextStep(action) {
            if(action == 1){
                if(vm.pageViewLogic.address.sel_add_id != 0)
                    changeView('review');
            }
        }

        function getCart() {
            homeService.getExistingCart(user.usr_id)
                .then(function(res) {
                    // console.log(res);
                    // $localStorage['incomp_cart'] = cartService.cart.results;
                    var local_cart = $localStorage['incomp_cart'];
                    if (local_cart[0].cart_type == 1) {
                        vm.drugDetail = local_cart[0];
                        vm.otcDetail = local_cart[1];
                    } else {
                        vm.otcDetail = local_cart[0];
                        vm.drugDetail = local_cart[1];
                    }
                    vm.tot = parseInt(vm.drugDetail.tot_presc_amt) + parseInt(vm.otcDetail.tot_otc_amount) + 10;
                    // console.log(vm.otcDetail , vm.drugDetail)
                })
        }

        function prep_for_add() {
            vm.pageViewLogic.isExistingCust = false;
            vm.pageViewLogic.showAddressNew = true;
        }

        // function createMasterCart(data) {
        //     if (angular.isUndefined($localStorage['masterCart'])) {
        //         var cart_ids = {
        //             'otc_cart_id': '',
        //             'user': $localStorage['usr'].usr_id,
        //             'presc_cart_id': ''
        //         }
        //         if (data[0].cart_type == 1) {
        //             cart_ids.presc_cart_id = data[0].id;
        //             cart_ids.otc_cart_id = data[1].id;
        //         } else {
        //             cart_ids.presc_cart_id = data[1].id;
        //             cart_ids.otc_cart_id = data[0].id;
        //         }
        //         cartService.createMasterCart(cart_ids)
        //             .then(function(res) {
        //                 console.log(res);
        //                 getMasterCartID();
        //             }, function(err) {
        //                 console.log('errorn in creating new master cart', err);
        //             })
        //     } else {
        //         console.log('attemting to create master cart id again');
        //     }
        // }

        function getMasterCartID() {
            viewCartService.getMasterCart(user.usr_id)
                .then(function(res) {
                    $localStorage['masterCart'] = viewCartService.isMasterCartExist[0];
                }, function(err) {
                    console.log(err);
                })
        }

        function updateDrug(item, flag) {
            if (flag && item.quantity <= 100) {
                item.quantity++;
                item.temprice = item.price * item.quantity;
            } else {
                if (item.quantity <= 1) {
                    del_selected_drug(item);
                } else {
                    item.quantity--;
                    item.temprice = item.temprice - item.price;
                }
            }
        }

        function del_selected_drug(item) {
            var index = id(vm.drugDetail, item);
            if (index != -1) {
                vm.drugDetail.splice(index, 1);
            }
        }

        function id(list, item) {
            if (list.length != 0) {
                for (var i = 0; i < list.length; i++) {
                    if (list[i].product === item.product)
                        return i;
                    else
                        return -1;
                }
            } else {
                return -1;
            }
        }

        function openAck() {
            var modalInstance = $uibModal.open({
                animation: true,
                backdrop: 'static',
                templateUrl: 'app/components/template/orderAck.html',
                controller: 'templateController',
                controllerAs: 'mod',
                size: 'lg'
            });

            modalInstance.result.then(function(selectedItem) {
                customerCreate();
            }, function() {
                // $log.info('Modal dismissed at: ' + new Date());
            });
        }

        function isCustExist() {
            if (angular.isUndefined(vm.customerDetails)) {
                cartService.getCustomerIfExist(user.usr_id)
                    .then(function(res) {
                        if (angular.isDefined(res.data.results[0])) {
                            $localStorage['cust'] = res.data.results[0];
                            vm.customerDetails = res.data.results[0];
                            vm.pageViewLogic.address.showCustAddress = true;
                            vm.pageViewLogic.address.showAddressNew = false;
                            vm.pageViewLogic.address.showNewCust = false;
                            defaultAddSel(vm.customerDetails);
                        } else {
                            vm.pageViewLogic.address.showNewCust = true;
                            vm.customerDetails = undefined;
                            // console.log(vm.customerDetails);
                        }
                    }, function(res) {
                        vm.customerDetails = undefined;
                        vm.pageViewLogic.address.showNewCust = true;
                        // cartService.createCustomer()
                    })
            }else {
                defaultAddSel(vm.customerDetails);
            }
        }

        function createCust() {
            if (vm.custModel.address.address_line1 != '' && vm.custModel.address.address_line3 != '' && vm.custModel.address.pincode != '') {
                var data = {
                    'name': $localStorage['usr'].username,
                    'mobile': $localStorage['usr'].mobile_no,
                    'gender': vm.custModel.gender,
                    'user': $localStorage['usr'].usr_id,
                    'address': [vm.custModel.address],
                    'customerID': utilities.genID($localStorage['usr'].email)
                };
                // console.log(data);
                cartService.createCustomer(data)
                    .then(function(res) {
                        $localStorage['cust'] = res;
                        isCustExistd();
                    })
            }
        }

        function createOrder() {
            if (angular.isUndefined(vm.customerDetails)) {
                openAddressModel();
            } else {
                console.log(vm.customerDetails);
                var orderID = utilities.genID(user.usr_id + Date() + $localStorage['selectedShop'].id);
                var data = {
                    "total_amount": vm.tot,
                    "paid": false,
                    "user": $localStorage['usr'].usr_id,
                    "orderID": orderID,
                    "customer": vm.customerDetails.id,
                    "billing_address": vm.pageViewLogic.address.sel_bill_add_id,
                    "time_to_deliver": "2017-06-23 21:00",
                    "cart": $localStorage['masterCart'].id,
                    "pharmacy": $localStorage['selectedShop'].id,
                    "active": true,
                    "status": "1",
                    "shipping_address": vm.pageViewLogic.address.sel_ship_add_id,
                    "payment_mode": "1"
                }
                cartService.createOrder(data)
                    .then(function(res) {
                        $localStorage['order'] = res;
                        // $state.go('home');
                        openAck();
                    })
            }
        }

        function trash(data) {
            var index = hasID(vm.otcDetail, data);
            vm.otcDetail.splice(index, 1);
            $localStorage['selectedOTC'] = vm.otcDetail;
        }

        function changeQty(action, item) {
            var index = hasID(vm.otcDetail, item);
            if (action) {
                item.qty++
            } else {
                if (item.qty == 0 || item.qty == 1) {
                    item.qty = 0;
                    item.isAdded = false;
                } else if (item.qty > 1) {
                    item.qty--;
                }
            }
            vm.otcDetail[index] = item;
            $localStorage['selectedOTC'] = vm.otcDetail;
        }

        function hasID(list, item) {
            if (!angular.isUndefined(list)) {
                for (var i = 0; i < list.length; i++) {
                    var element = list[i];
                    if (item.productid == element.productid)
                        return i;
                }
            }
            return -1;
        }

    }
})(angular);
