(function(angular) {
  	'use strict';


  	angular.module('yoophawebapp')
    	.service('accountService', accountService);

	/** @ngInject */
	function accountService($q, $http, $localStorage, $uibModal, getData, utilities, postData) {

	    var service = {};
	    var url = '';
	    service.updateCust = updateCust;
	    service.createCust = createCust;
	    return service;

	    function updateCust(id, data){
	    	var deferred = $q.defer();
	    	url = 'http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/customer/' + id + '/edit/';
	    	postData.put(url, data)
	    		.then(function (res){
	    			deferred.resolve(res.data);
	    		}, function (err){
	    			deferred.reject(res.data);
	    		});
	    	return deferred.promise;
	    }

	    function createCust(data) {
	    	var deferred = $q.defer();
	    	url = 'http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/customer/create/';
	    	postData.put(url, data)
	    		.then(function (res){
	    			deferred.resolve(res.data);
	    		}, function (err){
	    			deferred.reject(res.data);
	    		});
	    	return deferred.promise;
	    }
	}
})(angular);
(function(angular) {
    'use strict';

    angular
        .module('yoophawebapp')
        .controller('accountController', accountController);

    /** @ngInject */
    function accountController($rootScope, $scope, $state, $localStorage, homeService, cartService, accountService) {
        var vm = this;
        vm.log_info;
        vm.isCustExist;
		vm.customerDetails = undefined;
        vm.existingCust = false;
        vm.saveChanges = saveChanges;
        init();

        function init() {
        	vm.log_info = $localStorage['usr'];
        	getUsr();
        }

        function getUsr(){
        	if(angular.isDefined(vm.log_info)){
        	var user_id = vm.log_info.usr_id;
        	homeService.getUser(user_id)
	        	.then(function (res) {
	        	   // console.log(res);
	        	   vm.log_info.username = homeService.user.username;
	        	   vm.log_info.email = homeService.user.email;
	        	   vm.log_info.mobile_no = homeService.user.mobile_no;
	        	   $localStorage['usr'] = vm.log_info;
	        	   getCust();
	        	}, function(res) {
	        	   console.log(res);
	        	 })
        	}
        }

        function getCust(){
		    cartService.getCustomerIfExist(vm.log_info.usr_id)
		        .then(function(res) {
		            console.log(res);
		            if (angular.isDefined(res.data.results[0])) {
		                $localStorage['cust'] = res.data.results[0];
		                vm.customerDetails = res.data.results[0];
		                vm.existingCust = true;
		            } else {
		            	vm.customerDetails.name = vm.log_info.username;
			            vm.customerDetails.mobile = vm.log_info.mobile_no;
			            vm.customerDetails.gender = 'M';	
		            }
		        }, function(res) {
		            vm.customerDetails.name = vm.log_info.username;
		            vm.customerDetails.mobile = vm.log_info.mobile_no;
		            vm.customerDetails.gender = 'M';
		        })
        }

        function saveChanges(){
        	var data = {
				  "name": vm.customerDetails.name,
				  "mobile": vm.customerDetails.mobile,
				  "gender": vm.customerDetails.gender,
				  "user": vm.customerDetails.user,
				  "address": [vm.customerDetails.address],
				  "customerID": vm.customerDetails.customerID
				};
        	if(vm.existingCust){
        		data.address = [];
        		accountService.updateCust(vm.customerDetails.id, data)
        			.then(function (res){
        				console.log(res);
        			});
        	} else {
        		//create new cust
        		accountService.createCust(data)
        			.then(function (res){
        				console.log(res);
        			})
        	}
        }
    }
})(angular);
