(function(angular) {
  'use strict';

  /**
   * @ngdoc service
   * @name ipMobile.postData
   * @description
   * # postData
   * Factory in the ipMobile.
   */
  angular.module('yoophawebapp')
    .factory('postData', postData);

  /** @ngInject */
  function postData($http, $localStorage) {
    var service = {
      async: function (url, data, isUrlEncode) {

        var encodedData = 'data=' + JSON.stringify(data);

        // var encodedData = 'data=' + angular.toJson(data);

        if (angular.isDefined(isUrlEncode)) {
          var serialStr = angular.toJson(data);
          serialStr = serialStr.replace(new RegExp(':', 'g'), '=').replace(new RegExp(',', 'g'), '&').replace('{', '').replace('}', '').replace(new RegExp('"', 'g'), '');
          encodedData = serialStr;
        }
        delete $http.defaults.headers.common['X-Requested-With'];
        $http.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
        var promise =  $http({
          url: url,
          method: 'POST',
          // headers: {
          //   'Content-Type' : 'application/json',    
          //   'Authorization': 'JWT ' + $localStorage['usr'].accessTok
          // },
          data: encodedData
        }).then(function (response) {
          return response;
        }, function (response) { // optional
          return response;
        });

        return promise;
      },
      del: function (url) {
        var promise =  $http({
        url : url,
        method : 'DELETE',
        headers : {
              'Content-Type' : 'application/json',    
              'Authorization': 'JWT ' + $localStorage['usr'].accessTok
              }
        }).then(function(response){
            return response;
        }, function(error){
            return error;
        });

        return promise;       
      },
      put: function (url, data) {
        var promise =  $http({
        url : url,
        method : 'PUT',
        headers : {
              'Content-Type' : 'application/json',    
              'Authorization': 'JWT ' + $localStorage['usr'].accessTok
        },
        data: data
        }).then(function(response){
            return response;
        }, function(error){
            return error;
        });

        return promise;
      }
    }

    return service;
  }

})(angular);

(function(angular) {
  'use strict';

  /**
   * @ngdoc service
   * @name medArun.getData
   * @description
   * # getData
   * Factory in the medArun.
   */
  angular.module('yoophawebapp')
    .factory('getData', getData);

  /** @ngInject */
  function getData($http, $log, $localStorage) {
    var myService = {
      async: function (url) {
        // url = 'json/sample.json';
        var promise = $http.get(url).then(function (response) {
          return response.data;
        });

        return promise;
      },

      asyncHead: function (url) {
        var promise = $http({
          method: 'GET',
          url: url,
          headers : {
            'Content-Type' : 'application/json',    
            'Authorization': 'JWT ' + $localStorage['usr'].accessTok
          }
        }).then(function (response) {
          return response;
        });

        return promise;
      },

      relQuery: function (url, sourceQuery) {
        $http.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
        
        var query = angular.copy(sourceQuery);
        // removeEmptyParams(query);
        $log.debug(angular.toJson(query));
        var promise = $http({
          method: 'POST',
          url: url,
          data: angular.toJson(query)
        }).then(function (response) {
          var output = angular.fromJson(angular.fromJson(response));
          // output.results.offset = query.offset;
          return output;
        });

        return promise;
      }
    };

    return myService;

    /**
     * Recursively removes empty parameters (objects||arrays) from container object
     * @param  {Object} obj Container object
     */
    function removeEmptyParams(obj) {
      var i;
      if (angular.isObject(obj)) {
        for (i in obj) {
          // because Object.keys(new Date()).length === 0;
          // we have to do some additional check
          if (Object.keys(obj[i]).length === 0 && (obj[i].constructor === Object || obj[i].constructor === Array)) {
            delete obj[i];
          } else {
            removeEmptyParams(obj[i]);
          }
        }
      }
    }
  }
})(angular);

